import { _decorator, Component } from "cc";
import { GameManager } from "game_manager/GameManager";
import { UIManager } from "ui_manager/UIManager";

const { ccclass, property } = _decorator;

@ccclass('GameApp')
export class GameApp extends Component {
    private isOngoing: boolean;

    public gameLaunch(): void {
        this.isOngoing = false;
        this.preloadAssets()
    }

    private preloadAssets() {
        //ResManager.getInstance().loadBundle("preload_res", () => {
        this.startGameLoop();
        //}, this);
    }

    private startGameLoop(): void {
        UIManager.getInstance().init();
        GameManager.getInstance().init();
        this.isOngoing = true;
    }

    protected update(deltaTime: number): void {
        if (!this.isOngoing) {
            return;
        }
        GameManager.getInstance().update(deltaTime);
    }
}


